.about {
    display: flex;
    width: 100%;
    height: 100vh;
}

.about__block{
    width: 30%;
    text-align: center;
    color: white;
    margin: auto;
}

.about__img {
    width: 45px;
    height: 45px;
    border: 5px solid white;
    border-radius: 50%;
    padding: 5px;
    fill: white;
    transition: .5s;
    -moz-transition: .5s;
    -webkit-transition: .5s;
    -o-transition: .5s;
}

.about__block:hover .about__img {
    transform: scale(1.2);
}
.about__title {
    font-size: 1.6em;
}
.about__text {
    font-weight: 200;
    color: rgb(201, 201, 201);
}

.about__btn {
    outline: none;
    background: transparent;
    padding: 10px 30px;
    border-radius: 25px;
    font-size: 1.2em;
    border: 1px solid rgba(255, 103, 104, 1);
    color: rgba(255,103,104,1);
    transition: .5s;
    -moz-transition: .5s;
    -webkit-transition: .5s;
    -o-transition: .5s;
    cursor: pointer;
}

.about__btn:hover, .about__btn:active{
    color: white;
    background-color: rgba(255,103,104,1);
}
.about__btn:active {
    transform: scale(0.95);
}