.contact {
    padding-top: 50px;
}
.contact h2 {
    color: white;
    text-align: center;
    padding-bottom: 30px;
}

.form {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin: 0 10%;
}
.form__input, .form__textarea {
    background: transparent;
    outline: none;
    border: 2px solid rgba(255, 255, 255, 0.5);
    border-radius: 10px;
    color: white;
    padding: 10px;
}

.form__textarea {
    height: 100px;
}


.form__input::placeholder, .form__textarea::placeholder{
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 14px;
    font-weight: 100;
}

.form__block {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}
.form__label {
    display: grid;
    width: 48%;
    color: white;
}
.form__label span {
    margin-bottom: 10px;
    font-size: 0.8em;
    font-weight: 600;
}
.form__label-textarea {
    margin: 0;  
    width: 100%;
}