header{
    display: flex;
    text-align: center;
    width: 90%;
    padding: 45px 10px;
    margin: 0 5% 100px;
    border-bottom: 1px solid rgb(165, 165, 165);
    justify-content: space-between;
}
header ul{
    list-style: none;
    display: flex;
    margin: 0;
}
li {
    margin: 0 10px;
}
.link {
    color: white;
    text-decoration: none;
}
.link:hover{
    color: rgb(165, 165, 165);
}
.content h1 {
    font-weight: 300;
    color: white;
    text-align: center;
}
.logo{
    user-select: none;
    color: white;
    letter-spacing: 2px;
    font-weight: 400;
}
.logo span{
    font-weight: 200;
}
.content__info{
    display: flex;
}

.content__info .info__block{
    width: 29%;
    margin: 40px 2%;
    justify-items: center;
    display: grid;
    text-align: center;
    color: white;
    padding: 15px 0;
    border-right: 1px solid rgb(165, 165, 165);
}   
.info__block:nth-child(3){
    border: none;
}
.content__info .info__block .info__title {
    font-size: 1.3em;
    padding-bottom: 5px;
}
.content__info .info__block .info__title::first-letter{
    font-size: 1.2em;                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                       
    color: red;
    font-weight: 500;
    transition: .5s;
} 
.info__img {
    width: 64px;
    height: 64px;
    fill: rgb(196, 196, 196);
    margin-bottom: 10px;
    transition: .5s;
    -moz-transition: .5s;
    -webkit-transition: .5s;
    -o-transition: .5s;
}
.info__block:hover .info__img {
    transform: scale(1.2);
    fill: red;
}

.info__text {
    width: 60%;
    font-weight: 300;
    font-size: 1.1em;
    color: rgb(206, 206, 206);
}